@use 'settings/variables' as v;

// RESPONSIVE
@mixin telefono-m {
    @media (min-width: map-get(v.$medidas, telefono-m)){
        @content;
    }
}
@mixin telefono-xl {
    @media (min-width: map-get(v.$medidas, telefono-xl)){
        @content;
    }
}
@mixin tablet {
    @media (min-width: map-get(v.$medidas, tablet)){
        @content;
    }
}
@mixin desktop {
    @media (min-width: map-get(v.$medidas, desktop)){
        @content;
    }
}
@mixin desktop-m {
    @media (min-width: map-get(v.$medidas, desktop-m)){
        @content;
    }
}
@mixin desktop-xl {
    @media (min-width: map-get(v.$medidas, desktop-xl)){
        @content;
    }
}
@mixin dark-mode {
    @media (prefers-color-scheme: dark){
        @content;
    }
}

// GRID
@mixin grid ($num-columnas, $espaciado-gap) {
    display: grid;
    grid-template-columns: repeat($num-columnas, 1fr);
    gap: $espaciado-gap;
}

// FLEX
@mixin flex ($flex-direction, $flex-wrap, $justify-content, $align-items) {
    display: flex;
    flex-flow: $flex-direction $flex-wrap;
    justify-content: $justify-content;
    align-items: $align-items;
}

// NEOMORFISMO
// https://neumorphism.io/ para usar los colores en fondo-sombra1-sombra2
@mixin neomorfismo ($border-radius, $eje, $color-fondo, $color-sombra1, $color-sombra2) {
    border-radius: $border-radius;
    background: $color-fondo;
    box-shadow: $eje $eje ($eje*2) $color-sombra1, (-$eje) (-$eje) ($eje*2) $color-sombra2;
}

// POSITION
@mixin position ($position, $top:null, $right:null, $bottom:null, $left:null) {
    position: $position;
    top: $top;
    right: $right;
    bottom: $bottom;
    left: $left;
}

// DARKMODE @media (prefers-color-scheme: dark)
// gracias a https://www.fabrizioduroni.it/2020/05/20/dark-mode-css-sass-scss/
@mixin themeProperty($theme, $property, $color, $additionalPropertiesPositionIsFront, $additionalProperties) {
    @if $additionalPropertiesPositionIsFront {
      #{$property}: unquote($additionalProperties + ' ' + map-get($theme, $color));
    } @else {
      #{$property}: unquote(map-get($theme, $color) + ' ' + $additionalProperties);
    }
}
  
@mixin theme($property, $color, $additionalPropertiesPositionIsFront: false, $additionalProperties: '') {
    $light: map-get(v.$themes, light-theme);
    $dark: map-get(v.$themes, dark-theme);
  
    @include themeProperty($light, $property, $color, $additionalPropertiesPositionIsFront, $additionalProperties);
  
    @media (prefers-color-scheme: dark) {
      @include themeProperty($dark, $property, $color, $additionalPropertiesPositionIsFront, $additionalProperties);
    }
}
